import React from 'react';

import copySvg from '../images/copy.svg'
import inputSvg from '../images/input.svg'
import downloadSvg from '../images/download-black.svg'

function HowToConvert() {
    const steps = [
        {
            icon: copySvg,
            heading: '  URL ',
            desc: '  YouTube-'
        },
        {
            icon: inputSvg,
            heading: '  ',
            desc: '         ""'
        },
        {
            icon: downloadSvg,
            heading: ' MP3 ',
            desc: '    ""'
        }
    ]

    return <>
        <h1 className='text-[20px] text-center mt-16 mb-8'>  YOUTUBE-  MP3 </h1>
        <div className="grid md:grid-cols-3 gap-4">
            {
                steps.map(step => (
                    <div key={step.heading} className="flex flex-col items-center text-center rounded-2xl bg-white xl:p-16 lg:p-10 p-6">
                        <img src={step.icon} alt={step.heading} className="opacity-30 w-[32px] h-[32px]" />
                        <h3 className='text-[14px] mt-4 mb-3'>{step.heading}</h3>
                        <p className='opacity-60 color-text'>{step.desc}</p>
                    </div>
                ))
            }
        </div>

        <div className='mt-10'>
            <p className='text-center color-text'>
                Youtubetomp3.ge      YouTube  MP3    320 kbps-.      MP4, MP3, FLV, M4V, WMV  WEBM.     YouTube-    MP3   .
            </p>
        </div>
    </>;
}

export default HowToConvert;
